:root {
  --primary: #8b5cf6;
  --secondary: #06b6d4;
  --success: #10b981;
  --warning: #f59e0b;
  --danger: #ef4444;
  --bg-primary: #1a1a1a;
  --bg-secondary: #262626;
  --text-primary: #fff;
  --text-secondary: #d1d5db;
}

* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

html, body {
  width: 100%;
  height: 100%;
  overflow-x: hidden;
}

body {
  color: #fff;
  letter-spacing: .3px;
  background-color: #1a1a1a;
  font-family: -apple-system, BlinkMacSystemFont, Segoe UI, Roboto, Helvetica Neue, Arial, sans-serif;
  line-height: 1.6;
}

.app-container {
  background: linear-gradient(135deg, #1a1a1a 0%, #262626 100%);
  flex-direction: column;
  min-height: 100vh;
  display: flex;
}

.navbar-container {
  z-index: 100;
  background: linear-gradient(135deg, #1e1b4b 0%, #312e81 100%);
  border-bottom: 1px solid #8b5cf64d;
  width: 100%;
  min-height: 60px;
  display: block;
  position: sticky;
  top: 0;
  box-shadow: 0 4px 20px #0000004d;
}

.navbar {
  flex-direction: column;
  width: 100%;
  max-width: 1400px;
  min-height: 60px;
  margin: 0 auto;
  padding: 0;
  display: flex;
}

.nav-brand {
  border-bottom: 1px solid #8b5cf633;
  align-items: center;
  gap: 12px;
  padding: 16px 24px;
  display: flex;
}

.nav-brand .brand-text {
  -webkit-text-fill-color: transparent;
  letter-spacing: -.5px;
  background: linear-gradient(135deg, #a78bfa 0%, #06b6d4 100%);
  -webkit-background-clip: text;
  background-clip: text;
  margin: 0;
  font-size: 1.8em;
  font-weight: 800;
}

.nav-brand .brand-subtitle {
  color: #fff9;
  border-left: 2px solid #8b5cf666;
  margin: 0;
  padding-left: 12px;
  font-size: .85em;
  font-weight: 400;
}

.nav-sections {
  gap: 0;
  width: 100%;
  display: flex;
}

.nav-section {
  flex-direction: column;
  flex: 1;
  display: flex;
}

.nav-section.coffee-section {
  background: #8b5cf60d;
}

.nav-section.python-section {
  background: #06b6d40d;
}

.nav-section .section-title {
  color: #ffffff80;
  text-transform: uppercase;
  letter-spacing: 1px;
  background: #0003;
  margin: 0;
  padding: 8px 16px 4px;
  font-size: .7em;
  font-weight: 700;
}

.nav-menu {
  flex-wrap: wrap;
  gap: 0;
  margin: 0;
  padding: 0;
  list-style: none;
  display: flex;
}

.nav-item {
  border-right: 1px solid #ffffff0d;
  align-items: center;
  display: flex;
  position: relative;
}

.nav-item:last-child {
  border-right: none;
}

.nav-item .nav-link {
  color: #ffffffb3;
  white-space: nowrap;
  align-items: center;
  gap: 6px;
  padding: 12px 14px;
  font-size: .85em;
  text-decoration: none;
  transition: all .15s ease-in-out;
  display: flex;
  position: relative;
}

.nav-item .nav-link .nav-icon {
  font-size: 1.2em;
}

.nav-item .nav-link .nav-label {
  font-weight: 500;
}

.nav-item .nav-link:hover {
  color: #fff;
  background: #8b5cf626;
}

.nav-item .nav-link:after {
  content: "";
  background: #8b5cf6;
  height: 3px;
  transition: transform .15s ease-in-out;
  position: absolute;
  bottom: 0;
  left: 0;
  right: 0;
  transform: scaleX(0);
}

.nav-item.active .nav-link {
  color: #fff;
  background: #8b5cf633;
  font-weight: 600;
}

.nav-item.active .nav-link:after {
  transform: scaleX(1);
}

@media (width <= 768px) {
  .navbar {
    flex-direction: column;
    min-height: auto;
    padding: 10px 12px;
  }

  .nav-brand {
    justify-content: center;
    width: 100%;
    padding: 10px 0;
  }

  .nav-sections {
    flex-direction: column;
    gap: 0;
    width: 100%;
  }

  .nav-section {
    width: 100%;
  }

  .nav-section .section-title {
    margin: 8px 12px 4px;
    font-size: .7em;
  }

  .nav-menu {
    width: 100%;
    padding-bottom: 5px;
    overflow-x: auto;
  }

  .nav-menu::-webkit-scrollbar {
    height: 4px;
  }

  .nav-menu::-webkit-scrollbar-thumb {
    background: #8b5cf64d;
    border-radius: 4px;
  }

  .nav-item {
    height: 50px;
  }

  .nav-item .nav-link {
    height: 50px;
    padding: 8px 12px;
    font-size: .85em;
  }

  .nav-item .nav-link .nav-icon {
    font-size: 1.1em;
  }

  .nav-item .nav-link .nav-label {
    font-size: .85em;
  }
}

.app-header {
  text-align: center;
  background: linear-gradient(135deg, #8b5cf6 0%, #7c3aed 100%);
  padding: 40px 20px;
  position: relative;
  overflow: hidden;
  box-shadow: 0 8px 24px #0003;
}

.app-header:before {
  content: "";
  pointer-events: none;
  background: #ffffff0d;
  border-radius: 50%;
  width: 300px;
  height: 300px;
  position: absolute;
  top: -50%;
  right: -10%;
}

.app-header .header-content {
  z-index: 1;
  max-width: 800px;
  margin: 0 auto;
  position: relative;
}

.app-header h1 {
  letter-spacing: -1px;
  text-shadow: 0 2px 4px #0003;
  margin-bottom: 12px;
  font-size: 3em;
  font-weight: 700;
}

.app-header .subtitle {
  opacity: .9;
  font-size: 1.1em;
  font-weight: 300;
}

.app-main {
  flex: 1;
  width: 100%;
  max-width: 1400px;
  margin: 0 auto;
  padding: 30px 20px 40px;
}

.page-container {
  width: 100%;
}

.tutorial-content {
  background: #262626;
  border: 1px solid #404040;
  border-radius: 16px;
  padding: 40px;
  box-shadow: 0 4px 12px #00000026;
}

.tutorial-content h1 {
  color: #8b5cf6;
  border-bottom: 2px solid #8b5cf6;
  margin-bottom: 30px;
  padding-bottom: 20px;
  font-size: 2.5em;
}

.tutorial-content h2 {
  color: #8b5cf6;
  margin-top: 30px;
  margin-bottom: 16px;
  font-size: 1.6em;
}

.tutorial-content h3 {
  color: #06b6d4;
  margin-top: 24px;
  margin-bottom: 12px;
  font-size: 1.3em;
}

.tutorial-content h4 {
  color: #d1d5db;
  margin-top: 16px;
  margin-bottom: 8px;
  font-size: 1.1em;
}

.tutorial-content h5, .tutorial-content h6 {
  color: #d1d5db;
  margin-top: 12px;
  margin-bottom: 6px;
  font-size: 1em;
}

.tutorial-content p {
  color: #d1d5db;
  margin-bottom: 16px;
  line-height: 1.8;
}

.tutorial-content ul, .tutorial-content ol {
  margin-bottom: 16px;
  margin-left: 20px;
}

.tutorial-content ul li, .tutorial-content ol li {
  color: #d1d5db;
  margin-bottom: 8px;
  line-height: 1.6;
}

.tutorial-content table, .tutorial-content .markdown-table {
  border-collapse: collapse;
  background: #333;
  border-radius: 12px;
  width: 100%;
  margin: 20px 0;
  overflow: hidden;
  box-shadow: 0 2px 4px #0000001a;
}

.tutorial-content table th, .tutorial-content table td, .tutorial-content .markdown-table th, .tutorial-content .markdown-table td {
  text-align: left;
  color: #d1d5db;
  border-bottom: 1px solid #404040;
  padding: 12px 16px;
}

.tutorial-content table th, .tutorial-content .markdown-table th {
  color: #8b5cf6;
  text-transform: uppercase;
  letter-spacing: .5px;
  background: #8b5cf61a;
  font-size: .95em;
  font-weight: 600;
}

.tutorial-content table tbody tr, .tutorial-content .markdown-table tbody tr {
  transition: background .2s;
}

.tutorial-content table tbody tr:hover, .tutorial-content .markdown-table tbody tr:hover {
  background: #8b5cf60d;
}

.tutorial-content table tbody tr:last-child td, .tutorial-content .markdown-table tbody tr:last-child td {
  border-bottom: none;
}

.tutorial-content table code, .tutorial-content .markdown-table code {
  background: #26262680;
  border-radius: 6px;
  padding: 2px 6px;
  font-size: .85em;
}

.tutorial-content code {
  color: #06b6d4;
  background: #333;
  border-radius: 6px;
  padding: 2px 6px;
  font-family: Courier New, monospace;
  font-size: .9em;
}

.tutorial-content .code-block, .tutorial-content pre[class*="language-"] {
  border: 1px solid #404040;
  border-radius: 12px;
  overflow-x: auto;
  box-shadow: 0 2px 4px #0000001a;
  background: #2d2d2d !important;
  margin: 20px 0 !important;
  padding: 16px !important;
}

.tutorial-content .code-block code, .tutorial-content pre[class*="language-"] code {
  text-shadow: none;
  padding: 0;
  font-family: Consolas, Monaco, Courier New, monospace;
  font-size: .9em;
  line-height: 1.6;
  background: none !important;
}

.tutorial-content code[class*="language-"], .tutorial-content pre[class*="language-"] {
  text-shadow: none;
  font-family: Consolas, Monaco, Courier New, monospace;
}

.tutorial-content a {
  color: #06b6d4;
  border-bottom: 1px solid #0000;
  text-decoration: none;
  transition: all .15s ease-in-out;
}

.tutorial-content a:hover {
  border-bottom-color: #06b6d4;
}

.tutorial-content strong {
  color: #8b5cf6;
  font-weight: 600;
}

.tutorial-content em {
  color: #06b6d4;
  font-style: italic;
}

.tutorial-content pre {
  background: #333;
  border-radius: 12px;
  margin-bottom: 20px;
  padding: 16px;
  overflow-x: auto;
}

.tutorial-content hr {
  border: none;
  border-top: 2px solid #404040;
  margin: 24px 0;
}

@media (width <= 768px) {
  .tutorial-content {
    padding: 20px;
  }

  .tutorial-content h1 {
    font-size: 1.8em;
  }

  .tutorial-content h2 {
    font-size: 1.5em;
  }

  .tutorial-content h3 {
    font-size: 1.2em;
  }

  .app-main {
    padding: 20px 12px;
  }
}

.todo-page {
  width: 100%;
}

.input-section {
  margin-bottom: 30px;
}

.input-form .input-group {
  background: #262626;
  border: 1px solid #404040;
  border-radius: 16px;
  flex-wrap: wrap;
  gap: 12px;
  padding: 16px;
  transition: all .25s ease-in-out;
  display: flex;
  box-shadow: 0 4px 12px #00000026;
}

.input-form .input-group:focus-within {
  border-color: #8b5cf6;
  box-shadow: 0 0 0 3px #8b5cf61a, 0 4px 12px #00000026;
}

.input-form .todo-input, .input-form .category-select, .input-form .priority-select {
  color: #fff;
  background: #333;
  border: 1px solid #404040;
  border-radius: 12px;
  padding: 12px 16px;
  font-family: inherit;
  font-size: 1em;
  transition: all .15s ease-in-out;
}

.input-form .todo-input:focus, .input-form .category-select:focus, .input-form .priority-select:focus {
  border-color: #8b5cf6;
  outline: none;
  box-shadow: 0 0 0 3px #8b5cf61a;
}

.input-form .todo-input::placeholder, .input-form .category-select::placeholder, .input-form .priority-select::placeholder {
  color: #9ca3af;
}

.input-form .todo-input {
  flex: 1;
  min-width: 250px;
}

.input-form .category-select, .input-form .priority-select {
  min-width: 120px;
}

.input-form .add-button {
  color: #fff;
  cursor: pointer;
  white-space: nowrap;
  background: linear-gradient(135deg, #8b5cf6 0%, #7c3aed 100%);
  border: none;
  border-radius: 12px;
  align-items: center;
  gap: 8px;
  padding: 12px 24px;
  font-size: 1em;
  font-weight: 600;
  transition: all .15s ease-in-out;
  display: flex;
  box-shadow: 0 4px 12px #00000026;
}

.input-form .add-button:hover {
  transform: translateY(-2px);
  box-shadow: 0 8px 24px #0003;
}

.input-form .add-button:active {
  transform: translateY(0);
}

.input-form .add-button .icon {
  font-size: 1.3em;
}

.control-panel {
  background: #262626;
  border: 1px solid #404040;
  border-radius: 16px;
  flex-wrap: wrap;
  align-items: center;
  gap: 12px;
  margin-bottom: 30px;
  padding: 16px;
  display: flex;
}

.filter-group {
  gap: 8px;
  display: flex;
}

.filter-btn {
  color: #d1d5db;
  cursor: pointer;
  background: none;
  border: 1px solid #404040;
  border-radius: 12px;
  padding: 8px 16px;
  font-size: .95em;
  font-weight: 500;
  transition: all .15s ease-in-out;
}

.filter-btn:hover {
  color: #8b5cf6;
  border-color: #8b5cf6;
}

.filter-btn.active {
  color: #fff;
  background: #8b5cf6;
  border-color: #8b5cf6;
  box-shadow: 0 0 0 3px #8b5cf61a;
}

.category-filter, .category-dropdown {
  color: #fff;
  cursor: pointer;
  background: #333;
  border: 1px solid #404040;
  border-radius: 12px;
  padding: 8px 12px;
  font-family: inherit;
  transition: all .15s ease-in-out;
}

.category-filter:hover, .category-dropdown:hover {
  border-color: #8b5cf6;
}

.category-filter:focus, .category-dropdown:focus {
  border-color: #8b5cf6;
  outline: none;
  box-shadow: 0 0 0 3px #8b5cf61a;
}

.search-box {
  color: #fff;
  background: #333;
  border: 1px solid #404040;
  border-radius: 12px;
  flex: 1;
  min-width: 200px;
  padding: 8px 16px;
  font-size: .95em;
  transition: all .15s ease-in-out;
}

.search-box:focus {
  border-color: #8b5cf6;
  outline: none;
  box-shadow: 0 0 0 3px #8b5cf61a;
}

.search-box::placeholder {
  color: #9ca3af;
}

.clear-btn {
  color: #ef4444;
  cursor: pointer;
  background: none;
  border: 1px solid #ef4444;
  border-radius: 12px;
  padding: 8px 16px;
  font-weight: 500;
  transition: all .15s ease-in-out;
}

.clear-btn:hover {
  background: #ef44441a;
  border-color: #ef4444;
}

.content-wrapper {
  grid-template-columns: 1fr 350px;
  gap: 30px;
  margin-top: 20px;
  display: grid;
}

@media (width <= 1024px) {
  .content-wrapper {
    grid-template-columns: 1fr;
  }
}

.todos-section {
  min-height: 300px;
}

.list-container {
  min-height: 200px;
}

.empty-state {
  text-align: center;
  color: #9ca3af;
  padding: 60px 20px;
}

.empty-state p {
  font-size: 1.3em;
}

.todos-container {
  flex-direction: column;
  gap: 12px;
  display: flex;
}

.todo-item {
  cursor: default;
  background: #262626;
  border: 1px solid #404040;
  border-radius: 12px;
  justify-content: space-between;
  align-items: flex-start;
  gap: 16px;
  padding: 16px;
  transition: all .15s ease-in-out;
  animation: .25s ease-in-out slideIn;
  display: flex;
}

.todo-item:hover {
  background: #2e2e2e;
  border-color: #8b5cf6;
  box-shadow: 0 4px 12px #00000026;
}

.todo-item.completed {
  opacity: .6;
}

.todo-item.completed .todo-text {
  color: #9ca3af;
  text-decoration: line-through;
}

.todo-item.overdue {
  background: #ef44440d;
  border-color: #ef4444;
}

.todo-item.overdue .priority-indicator {
  animation: 2s infinite pulse-danger;
}

@keyframes pulse-danger {
  0%, 100% {
    opacity: 1;
  }

  50% {
    opacity: .5;
  }
}

@keyframes slideIn {
  from {
    opacity: 0;
    transform: translateY(10px);
  }

  to {
    opacity: 1;
    transform: translateY(0);
  }
}

.todo-content {
  flex: 1;
  gap: 16px;
  display: flex;
}

.todo-content .todo-checkbox {
  cursor: pointer;
  accent-color: #8b5cf6;
  flex-shrink: 0;
  width: 22px;
  height: 22px;
  margin-top: 2px;
}

.todo-content .todo-details {
  flex: 1;
  min-width: 0;
}

.todo-header {
  flex-wrap: wrap;
  align-items: center;
  gap: 12px;
  margin-bottom: 8px;
  display: flex;
}

.priority-indicator, .category-badge {
  flex-shrink: 0;
  font-size: 1.2em;
}

.todo-text {
  color: #fff;
  word-break: break-word;
  flex-grow: 1;
  font-size: 1.05em;
  font-weight: 500;
}

.due-date {
  color: #9ca3af;
  white-space: nowrap;
  background: #8b5cf61a;
  border-radius: 6px;
  padding: 4px 8px;
  font-size: .9em;
}

.todo-description {
  color: #d1d5db;
  padding: 8px 0;
  font-size: .95em;
  line-height: 1.5;
}

.todo-actions {
  flex-shrink: 0;
  gap: 8px;
  display: flex;
}

.todo-actions button {
  cursor: pointer;
  background: none;
  border: 1px solid #404040;
  border-radius: 6px;
  justify-content: center;
  align-items: center;
  padding: 8px 10px;
  font-size: 1.1em;
  transition: all .15s ease-in-out;
  display: flex;
}

.todo-actions button:hover {
  background: #333;
  border-color: #8b5cf6;
}

.todo-actions .delete-btn:hover {
  background: #ef44441a;
  border-color: #ef4444;
}

@media (width <= 1024px) {
  .stats-sidebar {
    grid-column: 1 / -1;
  }
}

.stats-panel {
  background: #262626;
  border: 1px solid #404040;
  border-radius: 16px;
  padding: 20px;
  position: sticky;
  top: 20px;
  box-shadow: 0 4px 12px #00000026;
}

@media (width <= 1024px) {
  .stats-panel {
    position: static;
  }
}

.stats-header {
  margin-bottom: 24px;
}

.stats-header h3 {
  color: #8b5cf6;
  margin-bottom: 16px;
  font-size: 1.3em;
}

.stat-summary {
  flex-direction: column;
  gap: 12px;
  display: flex;
}

.stat-item {
  background: #333;
  border-left: 3px solid #8b5cf6;
  border-radius: 12px;
  justify-content: space-between;
  align-items: center;
  padding: 12px;
  display: flex;
}

.stat-item.overdue {
  border-left-color: #ef4444;
}

.stat-item.overdue .stat-label {
  color: #ef4444;
}

.stat-label {
  color: #d1d5db;
  font-size: .9em;
}

.stat-value {
  color: #8b5cf6;
  font-size: 1.3em;
  font-weight: 700;
}

.progress-section {
  border-bottom: 1px solid #404040;
  margin-bottom: 24px;
  padding-bottom: 24px;
}

.progress-header {
  justify-content: space-between;
  align-items: center;
  margin-bottom: 12px;
  display: flex;
}

.progress-label {
  color: #d1d5db;
  font-weight: 500;
}

.progress-percentage {
  color: #8b5cf6;
  font-size: 1.3em;
  font-weight: 700;
}

.progress-bar {
  background: #333;
  border-radius: 4px;
  height: 8px;
  overflow: hidden;
}

.progress-bar .progress-fill {
  border-radius: 4px;
  height: 100%;
  transition: width .35s ease-in-out, background-color .25s ease-in-out;
  box-shadow: 0 0 10px;
}

.stats-section {
  margin-bottom: 20px;
}

.stats-section:last-child {
  margin-bottom: 0;
}

.stats-section h4 {
  color: #d1d5db;
  text-transform: uppercase;
  letter-spacing: 1px;
  margin-bottom: 12px;
  font-size: .95em;
}

.stat-badges {
  flex-direction: column;
  gap: 8px;
  display: flex;
}

.stat-badge {
  color: #d1d5db;
  background: #333;
  border-left: 3px solid #06b6d4;
  border-radius: 12px;
  padding: 8px 12px;
  font-size: .9em;
}

.stat-badge.priority-high {
  border-left-color: #ef4444;
}

.stat-badge.priority-medium {
  border-left-color: #f59e0b;
}

.stat-badge.priority-low {
  border-left-color: #10b981;
}

.stat-badge strong {
  color: #fff;
  font-weight: 600;
}

.app-footer {
  text-align: center;
  color: #9ca3af;
  background: #262626;
  border-top: 1px solid #404040;
  margin-top: auto;
  padding: 24px;
}

.app-footer p {
  margin: 8px 0;
}

.app-footer .keyboard-hint {
  margin-top: 12px;
  font-size: .9em;
}

.app-footer kbd {
  color: #fff;
  background: #333;
  border: 1px solid #404040;
